import {
	imageBase64
} from "@/api/public";
import {
	getProductCode, // code
} from "@/api/store.js";
import {
	scombinationCode, // code
	seckillCode // 
} from '@/api/activity.js';
import i18n from '../utils/lang.js';
let sysHeight = uni.getSystemInfoSync().statusBarHeight + 'px';
export const sharePoster = {
	data() {
		return {
			//
			codeShow: false,
			cid: '1',
			codeVal: "", // 
			size: 200, // 
			unit: 'upx', // 
			background: '#FFF', // 
			foreground: '#000', // 
			pdground: '#000', // 
			codeIcon: '', // 
			iconsize: 40, // 
			lv: 3, //   
			onval: true, // val
			loadMake: true, // 
			base64Show: 0,
			shareQrcode: 0,
			followCode: '',
			selectSku: {},
			currentPage: false,
			sysHeight: sysHeight,
			isShow: 0,
			storeImageBase64: ''
		};
	},
	methods: {
		qrR(res) {









		},
		getImageBase64() {
			let that = this;
			imageBase64(that.storeImage, this.storeInfo.wechat_code)
				.then((res) => {
					that.storeImageBase64 = res.data.image;
					if (this.storeInfo.wechat_code) {
						that.PromotionCode = res.data.code;
					}
				})
				.catch(() => {});
		},
		initPoster(arr2) {
			let that = this;
			uni.getImageInfo({
				src: that.PromotionCode,
				success() {
					if (arr2[2] == "") {
						//
						that.downloadFilePromotionCode(function(
							msgPromotionCode) {
							arr2[2] = msgPromotionCode;
							if (arr2[2] == "")
								return that.$util.Tips({
									title: i18n.t(
										``
									),
								});
							that.$util.PosterCanvas(
								arr2,
								that.storeInfo.store_name,
								that.storeInfo.price,
								that.storeInfo.ot_price,
								function(tempFilePath) {
									that.$set(that,
										"posterImage",
										tempFilePath);
									that.$set(that,
										"posterImageStatus",
										true);
									that.$set(that,
										"canvasStatus",
										false);
									that.$set(that,
										"actionSheetHidden",
										!that
										.actionSheetHidden
									);
								}
							);
						});
					} else {
						//
						that.$nextTick(e => {
							that.$util.PosterCanvas(
								arr2,
								that.storeInfo.store_name,
								that.storeInfo.price,
								that.storeInfo.ot_price,
								function(tempFilePath) {
									that.$set(that,
										"posterImage",
										tempFilePath);
									that.$set(that,
										"posterImageStatus",
										true);
									that.$set(that,
										"canvasStatus",
										false);
									that.$set(that,
										"actionSheetHidden",
										!that
										.actionSheetHidden
									);
								}
							);
						})

					}
				},
				fail: function(res) {






					return that.$util.Tips({
						title: i18n.t(`,`),
					});

				},
			});
		},
		/**
		 * 
		 */
		async goPoster(type) {
			let that = this;
			that.posters = false;
			that.$set(that, "canvasStatus", true);
			let arr2

			let met = type === 'scombination' ? scombinationCode(that.id) : type === 'seckill' ? seckillCode(
				that
				.id) : getProductCode(that.id)
			met.then((res) => {
					uni.downloadFile({
						url: that.setDomain(res.data.code),
						success: function(res) {
							that.$set(that, "isDown", false);
							that.$set(that, "PromotionCode", res.tempFilePath)
							if (typeof successFn == "function")
								successFn && successFn(res.tempFilePath);
							arr2 = [that.posterbackgd, that.storeImage, that.PromotionCode];
							that.initPoster(arr2)
						},
						fail: function() {
							that.$set(that, "isDown", false);
							that.$set(that, "PromotionCode", "");
						},
					});
				})
				.catch((err) => {
					that.$set(that, "isDown", false);
					that.$set(that, "PromotionCode", "");
					return that.$util.Tips({
						title: err,
					});
				});









		},
		//
		setDomain(url) {
			url = url ? url.toString() : "";
			//,
			if (url.indexOf("https://") > -1) return url;
			else return url.replace("http://", "https://");
		},
		//
		downloadFilestoreImage() {
			let that = this;
			uni.downloadFile({
				url: that.setDomain(that.storeInfo.image),
				success: function(res) {
					that.storeImage = res.tempFilePath;
					that.storeImageBase64 = res.tempFilePath;
				},
				fail: function() {
					return that.$util.Tips({
						title: "",
					});
					that.storeImage = "";
				},
			});
		},
		/**
		 * 
		 * @param function successFn 
		 *
		 */
		downloadFilePromotionCode(successFn) {
			let that = this;

			getProductCode(that.id)
				.then((res) => {
					uni.downloadFile({
						url: that.setDomain(res.data.code),
						success: function(res) {
							that.$set(that, "isDown", false);
							that.$set(that, "PromotionCode", res.tempFilePath)
							if (typeof successFn == "function")
								successFn && successFn(res.tempFilePath);
						},
						fail: function() {
							that.$set(that, "isDown", false);
							that.$set(that, "PromotionCode", "");
						},
					});
				})
				.catch((err) => {
					that.$set(that, "isDown", false);
					that.$set(that, "PromotionCode", "");
					return that.$util.Tips({
						title: err,
					});
				});
















		},
	}
};
